.addQuestion {
    margin: auto;
    max-width: 400px;
    font: 17px "Lucida Sans Unicode", "Lucida Grande", sans-serif;
    padding: 20px;
    border: 2px;
    background-color: #E9E9E9;
    bottom: 10px;
    left: 50rem;
    position: absolute;
}
.addQuestion li {
    padding: 0;
    display: block;
    list-style: none;
    margin: 10px 0 0 0;
}

.addQuestion label{
    margin:0 0 10px 0;
    display:block;
    font-weight: bold;
}


input[type=text]{
    width: 50%;
    
}

input[type=text]:focus {
    background-color: lightblue;
}

@media screen and (max-width: 1200px) {
    .addQuestion {
      left: 1rem;
    }
}

.scroll{
    height: 850px;
    overflow-y: auto;
    direction: ltr;
}

@media screen and (max-width: 1200px) {
    .scroll {
      height: 500px;
    }
}

