.menu {
  overflow: hidden;
  background-color: #99D3DF;
  padding-bottom:15px;
}

html, body {
  width: 100%;
  height: 100%;
}

body{
  background-image: url("../images/background.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; 
}

.menu li{
  display: inline;
  margin-left: 5px;
}

.menu a {
  float: left;
  color: black;
  text-align: center;
  text-decoration: none;
  padding: 20px;
  font-size: 18px;
}

.menu a:hover {
  background-color: #88BBD6;
  color: black;
}

.menu a.active {
  background-color: #88BBD6;
  color: white;
  text-decoration: underline;
}