.gameBox{
    background-color: lightblue;
    width: 36%;
    padding: 10px;
    text-align: center;
    margin: auto;
    margin-top: 150px;
}


.styledButton{
    display:   inline-block;
    padding:   0.35em 1.2em;
    border:    0.1em solid Black;
    margin: 0 0.3em 0.3em 0;
    border-radius: 0.12em;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Roboto',sans-serif;
    font-weight: 300;
    color: Black;
    text-align: center;
    transition: all 0.2s;
}
.styledButton:hover{
    color:#000000;
    background-color: lightblue;
    cursor: pointer;
}
    
.active{
    background-color: #74ada2;
}

.activeGame button{
    width: 200px;
}